import * as fs from 'fs';
import * as os from 'os';
import * as path from 'path';

import * as gulp from 'gulp';
import * as yargs from 'yargs';

import * as coreBuild from '@microsoft/gulp-core-build';

import { SchemaValidator } from './jsonUtilities/SchemaValidator';
import { BuildRigConstants } from './BuildRigConstants';

/**
 * Represents a function which can be called using data in a file as the parameter,
 * with an optional schema for validation, and readmeURL for redirecting users
 * to help.
 *
 * @todo 253526 this will eventually be pushed directly into gulp-core-build
 */
export interface IConfigurableTask {
  configFile: string;
  schemaFile: string;
  readmeUrl?: string;
  callback: (config: Object) => void;
}

/**
 * The flags which this build rig defines. Extend this for child rigs
 */
export interface ICoreBuildArgs {
  // Note the production and ship flags should have the same value
  // @todo 264931 remove the production flag
  production: boolean;
  ship: boolean;
  // A special flag for then they run --tasks
  tasks: boolean;
  verbose: boolean;
}

/**
 * Represents the a task which is registered to the gulp command line
 * The arguments function is evaluated if this is the current task, it should
 * interact with yargs to define any custom values.
 *
 * @todo 178074 this code should be removed
 */
export interface ITaskDefinition {
  executable: coreBuild.IExecutable;
  arguments?: (args: yargs.Argv) => void;
}

// Helper function to lock the paths to schema files
function getSchemaFilePath(filename: string): string {
  return path.join(__dirname, 'schemas', filename);
}

/**
 * Represents a gulp-core-build build "rig", or a collection of tasks and
 * configurations. It can be extended to define more top-level configurable tasks,
 * inject subtasks into the chain, register arguments to the command line.
 *
 * This class represents all the logic which doesn't belong in a rig, and should instead
 * be a part of gulp-core-build. Thus, this class should be removed once that logic
 * is properly replicated in gulp-core-build.
 *
 * @todo 178074, 253519, 253526
 */
export abstract class BuildRig {
  protected args: ICoreBuildArgs;

  /**
   * The primary entry point into this build rig, accepts a gulp instance which is passed through to gulp-core-build
   */
  public initialize(gulp: gulp.Gulp): void {

    // Initialize yargs & figure out which command we are running
    const yargsArgs: yargs.Argv = this.getYargs();
    const command: string = yargsArgs.argv._[0];
    this.args = yargsArgs.argv;

    // Collect the tasks which need to be registered
    const tasks: Map<string, ITaskDefinition> = this.getTasks();

    // Reset the args and reconfigure based on the selected command
    yargsArgs.reset();
    if (tasks.has(command)) {
      const task: ITaskDefinition = tasks.get(command);
      if (task.arguments) {
        task.arguments(yargsArgs);
      }
    }

    // Note this overrides the getters for ship and production on args
    // @todo 264931 - the production flag should be removed
    this.args.ship = this.args.production = (this.args.production || this.args.ship);

    // Since gulp-core-build doesn't recognize the --ship flag, ensure it gets the right state
    coreBuild.mergeConfig({
      production: this.args.ship,
      shouldWarningsFailBuild: this.args.ship
    });

    // Register all the tasks with gulp-core-build
    tasks.forEach((definition: ITaskDefinition, name: string): void => {
      coreBuild.task(name, definition.executable);
    });

    // If they are just looking for the task list, save time by not reading config files
    if (!this.args.tasks) {
      // Configure tasks for a normal build
      console.log('Build target: ' + (this.args.ship ?
        BuildRigConstants.flavors.ship.toUpperCase() :
        BuildRigConstants.flavors.debug.toUpperCase()));

      this.setupSharedConfig();

      try {
        this._loadCustomConfigs();
      } catch (error) {
        console.log(error);
        throw error;
      }
      this.finalizeSharedConfig();
    }

    coreBuild.initialize(gulp);
  }

  /**
   * Override this function to create new top-level tasks which can be executed via the command line
   */
  protected abstract getTasks(): Map<string, ITaskDefinition>;

  /**
   * Override this function to do configuration before the configuration .json files have been loaded
   */
  protected abstract setupSharedConfig(): void;

  /**
   * Override this function to do configuration after the configuration .json files have been loadeds
   */
  protected abstract finalizeSharedConfig(): void;

  /**
   * Override this function to register more .json files which can be read, validated, and passed through
   * to a callback function. See IConfigurableTask for more information.
   * @todo 253526 push this code into gulp-core-build/the individual task definitions
   */
  protected getCustomConfigs(): Array<IConfigurableTask> {
    return [
      {
        callback: (props) => {
          coreBuild.mergeConfig({ properties: props });
        },
        configFile: 'config.json',
        schemaFile: getSchemaFilePath('config.schema.json')
      }
    ];
  };

  /**
   * Override this function to register more top-level command line arguments (i.e. not task-specific).
   * Ideally, extend the ICoreBuildArgs when hooking into this function
   * @todo 253519 : this code should be removed once we determine the interface for argument parsing with GCB
   */
  protected getYargs(): yargs.Argv {
    const yargsArgs: yargs.Argv = yargs.usage()
      .option('production', {
        alias: 'p',
        describe: 'build in ship mode with full localization and minimization',
        boolean: true
      })
      .option('ship', {
        alias: 'p',
        describe: 'build in ship mode with full localization and minimization',
        boolean: true
      })
      .option('verbose', {
        describe: 'run the build with verbose logging'
      })
      .option('tasks', {
        alias: ['T', 'tasks-simple'],
        describe: 'shows the list of tasks which can be run'
      })
      .help('h')
      .global(['production', 'verbose', 'h'])
      .usage([
        'CONFIG FILES'
      ].concat(
        this.getCustomConfigs().map((config: IConfigurableTask) => {
          return `   ${config.configFile}${os.EOL}${config.readmeUrl ? config.readmeUrl : ''}${os.EOL}`;
        }
      ))
      .join(os.EOL));
    return yargsArgs;
  }

  /**
   * Loads all custom configuration files from disk and applies them to the task configuration callback
   * @todo VSO #253526 - move this into each task, respectively
   */
  private _loadCustomConfigs(): void {
    for (const taskConfigInfo of this.getCustomConfigs()) {
      const configFilename: string = path.join(process.cwd(), 'config', taskConfigInfo.configFile);
      const schemaFilename: string = taskConfigInfo.schemaFile;
      const rawConfig: {} = this._readConfigFile(configFilename, schemaFilename, taskConfigInfo.readmeUrl);
      if (rawConfig) {
        taskConfigInfo.callback(rawConfig);
      }
    }
  }

  /**
   * Helper function which loads a custom config file from disk, runs the SchemaValidator on it,
   * and then apply it to the callback defined on the IConfigurableTask
   */
  private _readConfigFile(filename: string, schemafile: string, helpUrl: string): Object {
    if (!fs.existsSync(filename)) {
      return undefined;
    } else {
      if (this.args.verbose) {
        console.log(`Found config file: ${path.basename(filename)}`);
      }

      let data: {};
      try {
        if (fs.existsSync(schemafile)) {
          if (this.args.verbose) {
            console.log(`Schema file ${schemafile} not found`);
          }
          data = SchemaValidator.readAndValidateJson(filename, schemafile);
        } else {
          data = SchemaValidator.readCommentedJsonFile(filename);
        }
      } catch (error) {
        throw error.toString() +
          (helpUrl ? 'For more usage, please read:' + os.EOL + helpUrl : '');
      }

      return data;
    }
  }
}